import React, { Component } from 'react';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import { ThemeProvider as MuiThemeProvider } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

export class FormCountry extends Component {
  continue = e => {
    e.preventDefault();
    this.props.nextStep();
  };

  back = e => {
    e.preventDefault();
    this.props.prevStep();
  };

  render() {
    const { values, handleChange } = this.props;
    return (
      <MuiThemeProvider>
        <>
          <Dialog
            open
            fullWidth
            maxWidth='sm'
          >
            <AppBar title="Enter Your Country Name" />
            <TextField
              placeholder="Enter Your Country Name"
              label="Enter Your Country"
              onChange={handleChange('countryName')}
              defaultValue={values.countryName}
              margin="normal"
              fullWidth
            />
            
            <br />
            <AppBar title="Enter Your Favourite Place" />
            <TextField
              placeholder="Enter Your Favourite Place Name"
              label="Enter Favourite Place"
              onChange={handleChange('placeName')}
              defaultValue={values.placeName}
              margin="normal"
              fullWidth
            />
            
            <br />

            <Button
              color="secondary"
              variant="contained"
              onClick={this.back}
            >Back</Button>
           <br />
            <Button
              color="primary"
              variant="contained"
              onClick={this.continue}
            >Next</Button>
          </Dialog>
        </>
      </MuiThemeProvider>
    );
  }
}

export default FormCountry;
