import React, { Component } from 'react';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import { ThemeProvider as MuiThemeProvider } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

export class FormPersonalDetails extends Component {
  continue = e => {
    e.preventDefault();
    this.props.nextStep();
  };

  back = e => {
    e.preventDefault();
    this.props.prevStep();
  };

  render() {
    const { values, handleChange } = this.props;
    return (
      <MuiThemeProvider>
        <>
          <Dialog
            open
            fullWidth
            maxWidth='sm'
          >
            <AppBar title="Enter Your Favourite Writer" />
            <TextField
              placeholder="Enter Your Writer Name"
              label="Enter Favourite Writer"
              onChange={handleChange('writerName')}
              defaultValue={values.writerName}
              margin="normal"
              fullWidth
            />
            
            <br />
            <AppBar title="Enter Your Favourite Book" />
            <TextField
              placeholder="Enter Your Favourite Book Name"
              label="Enter Favourite Book"
              onChange={handleChange('bookName')}
              defaultValue={values.bookName}
              margin="normal"
              fullWidth
            />
            
            <br />

            <Button
              color="secondary"
              variant="contained"
              onClick={this.back}
            >Back</Button>
           <br />
            <Button
              color="primary"
              variant="contained"
              onClick={this.continue}
            >Next</Button>
          </Dialog>
        </>
      </MuiThemeProvider>
    );
  }
}

export default FormPersonalDetails;
